import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { fetchImages } from '../../actions/searchAction';
import { fetchOrder } from '../../actions/searchAction';

import IconMenu from 'material-ui/IconMenu';
import MenuItem from 'material-ui/MenuItem';
import IconButton from 'material-ui/IconButton';
import ContentSort from 'material-ui/svg-icons/content/sort';
import { white } from 'material-ui/styles/colors';

class ImgSortIcon extends Component {
  render() {
    const styles = {
      color: white,
    };
    return (
        <IconMenu
          iconButtonElement={
            <IconButton
              // tooltip='Sort by'
              // touch={true}
            >
              <ContentSort />
            </IconButton>
          }
          onChange={(e, value) => { this.props.fetchOrder(value); this.props.fetchImages() }}
          value={this.props.order}
          iconStyle={styles}
          anchorOrigin={{ horizontal: 'middle', vertical: 'center' }}
          targetOrigin={{ horizontal: 'left', vertical: 'top' }}
        >
          <MenuItem value='popular' primaryText='popular' />
          <MenuItem value='latest' primaryText='latest' />
        </IconMenu>
    )
  }
}

ImgSortIcon.propTypes = {
  fetchImages: PropTypes.func.isRequired,
  fetchOrder: PropTypes.func.isRequired,
  order: PropTypes.string.isRequired,
}

const mapStateToProps = state => ({
  order: state.search.order,
})

export default connect(mapStateToProps, { fetchImages, fetchOrder })(ImgSortIcon)


