import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { fetchMoreImages } from '../actions/searchAction';
import { fetchPage } from '../actions/searchAction';
import { fetchViewerOpen } from '../actions/imgViewerAction';
import { fetchViewerImg } from '../actions/imgViewerAction'; 
import { fetchWaitNextData } from '../actions/imgViewerAction';

import '../App.css';

import Loading from './Loading';

import Dialog from 'material-ui/Dialog';
import IconButton from 'material-ui/IconButton';
import { Toolbar, ToolbarGroup, } from 'material-ui/Toolbar';

// import OpenInNew from 'material-ui/svg-icons/action/open-in-new';
import NavigationChevronLeft from 'material-ui/svg-icons/navigation/chevron-left';
import NavigationChevronRight from 'material-ui/svg-icons/navigation/chevron-right';
import NavigationClose from 'material-ui/svg-icons/navigation/close';
import { grey400 } from 'material-ui/styles/colors';

// import pixabayLogo2 from '../img/pixabayLogo2.svg';
// import PixabayLogo_1 from '../img/pixabayLogo_1';

class ImgViewer extends Component {
  constructor(props) {
    super(props);
    this.handleNextButton = this.handleNextButton.bind(this);
  }
  
  componentDidMount() {
    window.addEventListener('keydown', this.handleNextButton);
    // window.addEventListener('',)
  }

  componentWillUnmount() {
    window.removeEventListener('keydown', this.handleNextButton);
  }

  handleImgOpen = (newIndex) => {
    this.props.fetchViewerImg(newIndex)
  }

  handleImgClose = () => {
    this.props.fetchViewerOpen(false, -1);
  }

  handleNextButton = (e = { keyCode: 0, key: '' }, side) => {
    let nextArrInd;
    if ((side === 'right') || (e.key === 'ArrowRight') || (e.keyCode === 39)) {
      nextArrInd = this.props.currentImgInd + 1
    } else if ((side === 'left') || (e.key === 'ArrowLeft') || (e.keyCode === 37)) {
      nextArrInd = this.props.currentImgInd - 1
    } else return null

    // if ((nextArrInd > -1) && (nextArrInd < (this.props.value.images.length))) {
    //   this.handleImgOpen(this.props.value.images[nextArrInd], nextArrInd)
    // } else {
    //   this.handleImgClose()
    // }

    if (nextArrInd > -1) {
      switch (true) {
        case (nextArrInd < this.props.images.length):
          this.props.fetchViewerImg(nextArrInd);
          break;
        case (nextArrInd === this.props.images.length):
          this.props.fetchWaitNextData(true);
          if (this.props.totalHits > this.props.images.length) {
            this.props.fetchPage(this.props.page + 1);
            this.props.fetchMoreImages(true);
          } else {
            this.handleImgClose()
          }
          break;
        default:
          break;
      }
    } else {
      this.handleImgClose()
    }
  }  

  render() {
    console.log('start render imageViewer');

    // hook for correct paddingTop when opening Dialog with dynamic content; correct Dialog display for 'portrait'&'landskape':
    const dialogStyle = {
      dialogRoot: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        paddingTop: 0,
        width: '100%',
        // maxWidth: 'none',
      },
      dialogContent: {
        position: 'relative',
        transform: 'translate(0px, 0px)',
        width: '100%',
        maxWidth: '99vw',
      },
      dialogBody: {
        padding: 2,
        objectFit: 'contain',
      },
      // my additional settings
      dialogImg: {
        maxWidth: '80vw',
        maxHeight: '80vh',
      },
      dialogOverlay: {
        backgroundColor: 'rgba(0, 0, 0, 0.94)',
      },
    };

    const styles = {
      viewerNavigation: {
        position:'fixed',
        zIndex: 1501,
        // transition
      },
      viewerIconButtonLR: {
        position: 'fixed',
        top: '35vh',
        // zIndex: 1501,
        // width: 'auto',
        width: '45vw',
        height: '30vh',
        padding: 3,
        cursor: 'pointer',
        // backgroundColor: 'rgba(0, 0, 0, 0.14)',
      },
      viewerIconButton: {
        position: 'fixed',
        maxHeight: '10vh',
        // top: 57,
        // right: 1,
        // zIndex: 1501,
        // width: 'auto',
        // width: '45vw',
        // height: '30vh',
        padding: 1,
        // backgroundColor: 'rgba(0, 0, 0, 0.14)',
      },
      viewerIconStyleLR: {
        // iconHoverColor: '#000',        
        width: '8vw',
        height: '8vw',
        maxHeight: 50,
        color: grey400,
      },
      viewerIconStyle: {
        // width: 54,
        // height: 'auto',
        color: grey400,
        float: 'right',
        height: '7vh', 
        width: '7vw',
        maxHeight: 30,
      },
      topToolBar: {
        position: 'fixed',
        top: 0,
        width: '100%',
        height: '9vh',
        // opacity: '0.8',
        backgroundColor: 'rgba(0, 0, 0, 0.5)',
      },
      imgCounter: {
        position: 'fixed',
        top: '2vh',
        left: '2vw',
        color: grey400,
        fontSize: 17,
      }
    };

    // const actions = [
      // <FlatButton
      //   label='Left'
      //   primary={true}
      //   onClick={() => this.handleNextButton({}, 'left')}
      // />,
      // <FlatButton
      //   label='Right'
      //   primary={true}
      //   onClick={() => this.handleNextButton({}, 'right')}
      // />,
      // <FlatButton
      //   label = 'Close'
      //   primary = {true}
      //   onClick = {this.handleImgClose}
      // />
    // ]
    
    let currentImg = this.props.images[this.props.currentImgInd].largeImageURL;

    let loadingBar;
    if (this.props.nextDataLoading) {
      window.removeEventListener('keydown', this.handleNextButton);
      loadingBar = <Loading />;
    } else {
      window.addEventListener('keydown', this.handleNextButton);
    }

    console.log('render imageViewer before Return');
    
    return (
      <div className='gridViewer'>
        <Dialog
          // actions={actions}
          modal={false}
          open={this.props.open}
          onRequestClose={this.handleImgClose}
          contentStyle={dialogStyle.dialogContent}
          bodyStyle={dialogStyle.dialogBody}
          style={dialogStyle.dialogRoot}
          repositionOnUpdate={false}
          autoDetectWindowHeight={false}
          overlayStyle={dialogStyle.dialogOverlay}
        >
          <img className='dialogIMG' src={currentImg} alt='' style={dialogStyle.dialogImg} />
          {/* <IconButton
            onClick={() => console.log(this.state.currentImgPageUrl)}
            style={styles.viewerIconButtonLink}
            iconStyle={styles.viewerIconStyle}
            // disableTouchRipple={true}
            href= {this.state.currentImgPageUrl}
          >
            <img src={pixabayLogo2} alt='link to details' />  
          </IconButton> */}

        </Dialog>
        <div className='viewerNavigation' style={styles.viewerNavigation}>
          <IconButton
            onClick={() => this.handleNextButton({}, 'left')}
            style={Object.assign({}, styles.viewerIconButtonLR, { left: 0, })}
            iconStyle={Object.assign({}, styles.viewerIconStyleLR, { float: 'left' })}
            disableTouchRipple={true}
          >
            <NavigationChevronLeft />
          </IconButton>
          <IconButton
            onClick={() => this.handleNextButton({}, 'right')}
            style={Object.assign({}, styles.viewerIconButtonLR, { right: 0, })}
            iconStyle={Object.assign({}, styles.viewerIconStyleLR, { float: 'right' })}
            disableTouchRipple={true}
          >
            <NavigationChevronRight/>
          </IconButton>
          <Toolbar style={styles.topToolBar}>

            {/* <ToolbarGroup firstChild={true}>
              <ToolbarTitle 
                text={this.props.totalHits}
                style={styles.topToolBarTitle}
            />
            </ToolbarGroup> */}
            <ToolbarGroup lastChild={true} >
            </ToolbarGroup>
          </Toolbar>
          <IconButton
            onClick={this.handleImgClose}
            style={Object.assign({}, styles.viewerIconButton, { top: 1, right: 3 })}
            iconStyle={Object.assign({}, styles.viewerIconStyle, {})}
            disableTouchRipple={true}
          >
            <NavigationClose />
          </IconButton>
          <div
            className='imgCounter'
            style={styles.imgCounter}>
            {/* <div style={{paddingTop:'2vh'}}> */}
            {(this.props.currentImgInd + 1) + ' / ' + this.props.totalHits}
            {/* </div> */}
          </div>
        </div>

        {/* <IconButton
          onClick={() => console.log(this.state.currentImgPageUrl)}
          style={styles.viewerIconButtonLink}
          iconStyle={styles.viewerIconStyle}
          // disableTouchRipple={true}
          href={this.state.currentImgPageUrl}
          target='_blank'
        > */}
        {/* <img src={pixabayLogo2} alt='link to details' /> */}
        {/* <PixabayLogo_1 /> */}
        {/* <svg>
            <use href={pixabayLogo2} />
          </svg> */}
        {/* </IconButton> */}
        {loadingBar}
      </div>
    )
  }
}

ImgViewer.propTypes = {
  fetchMoreImages: PropTypes.func,
  fetchPage: PropTypes.func,
  fetchViewerOpen: PropTypes.func,
  fetchViewerImg: PropTypes.func,
  fetchWaitNextData: PropTypes.func,
  images: PropTypes.array,
  totalHits: PropTypes.number,
  page: PropTypes.number,
  open: PropTypes.bool,
  currentImgInd: PropTypes.number.isRequired,
  nextDataLoading: PropTypes.bool,
}

const mapStateToProps = state => ({
  images: state.search.images,
  totalHits: state.search.total,
  page: state.search.page,
  open: state.imgViewer.open,
  currentImgInd: state.imgViewer.currentImgInd,
  nextDataLoading: state.imgViewer.nextDataLoading,
})

export default connect(mapStateToProps, { fetchMoreImages, fetchPage, fetchViewerOpen, fetchViewerImg, fetchWaitNextData })(ImgViewer)


