import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { fetchImages } from '../actions/searchAction';
import { fetchSearchText } from '../actions/searchAction';
import { fetchWindowWidth } from '../actions/searchAction';

import MenuDrawer from './Drawer';
import SearchFilters from './Filters';
// import FiltersBar from './FiltersBar';
import ImgSortIcon from './SearchFilters/ImgSortIcon';

import IconButton from 'material-ui/IconButton';
import TextField from 'material-ui/TextField';
import { Toolbar, ToolbarGroup, /*ToolbarSeparator, ToolbarTitle*/ } from 'material-ui/Toolbar';
// import Divider from 'material-ui/Divider';

import ActionSearch from 'material-ui/svg-icons/action/search';
import NavigationMenu from 'material-ui/svg-icons/navigation/menu';
import NavigationMoreVert from 'material-ui/svg-icons/navigation/more-vert';
import ExpandMore from 'material-ui/svg-icons/navigation/expand-more';
import ExpandLess from 'material-ui/svg-icons/navigation/expand-less';
// import ContentSort from 'material-ui/svg-icons/content/sort';
import { grey100, /*grey300,*/ grey500, /*grey800, deepOrange800,*/ white } from 'material-ui/styles/colors';

class NavBar extends Component {
  constructor(props) {
    super(props);
    this.state = {
      showDrawer : false,
      showFilters: false,
      windowTop: true,
    };
    this.handleResize = this.handleResize.bind(this);
    this.handleScroll = this.handleScroll.bind(this);
  }

  componentDidMount() {
    window.addEventListener('resize', this.handleResize);
    window.addEventListener('scroll', this.handleScroll);
    this.props.fetchImages();
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.handleResize);
    window.removeEventListener('scroll', this.handleScroll);
  }
  
  handleResize() {
    console.log('NabBar window.innerWidth', window.innerWidth);
    let width = window.innerWidth;
    let currentWidth;
    switch (true) {
      case (width >= 1600):
        currentWidth = 1
        break;
      case (width >= 1024):
        currentWidth = 2
        break;
      case (width >= 768):
        currentWidth = 3
        break;
      case (width >= 480):
        currentWidth = 4
        break;
      default:
        currentWidth = 5
    }
    if (this.props.designType === currentWidth){
      return null
    } else {
      (currentWidth === 5) ? (this.setState({ showFilters: false })) : null;
      this.props.fetchWindowWidth(currentWidth)
    }
  }

  handleScroll() {
    if (window.pageYOffset < 10) {
      this.setState({
        windowTop: true,
        showFilters: false,
      })
    } else {
      this.setState({
        windowTop: false,
      })
    }
  }

  handleMenuButtonClick = () => {
    this.setState({
      showDrawer: !this.state.showDrawer,
    })
  }

  handleFiltersButtonClick = () => {
    this.setState({
      showFilters: !this.state.showFilters,
    })
  }

  render() {
    console.log('I am NavBar');
    console.log(this.props.designType);

    const styles = {
      filtersButton: {
        height: 32,
        width: 32,
        padding: '0',
        // borderLeft: '',
      },
      filtersIconColor: {
        color: '#263238',
      },
      hintStyle: {
        // color: white,
        fontSize: 17,
        // textAlign: 'center',
        // width: '100%',
        // margin: '0px 0px 10px 0px',
        height: 16,
        paddingLeft: 5,
        color: grey500
      },
      inputStyle: {
        fontSize: 17,
        // color: white,
        //   // textAlign: 'center',
        //   // width: '100%',
        //   // margin: '0px 0px 20px 0px',
        //   borderRadius: 2,
        //   // backgroundColor: grey700,
        //   height: 35,
        paddingLeft: 5
      },
      navBar: {
        // backgroundColor: grey800,
        // backgroundColor: deepOrange800,

        // position: 'fixed',
        // top: 0,
        // width: '100%',
        // justifyContent: 
      },
      navBarIcons: {
        color: white,
      },
      rootComponent: {
        position: 'fixed',
        width: '100%',
        top: 0,
        zIndex: 1001,
      },
      searchField: {
        // backgroundColor: grey100,
        // borderRadius: 1,
        height: 32,
        // width: '80%',
      },
      searchFiltersTop: {
        position: 'relative',
        top: 57,
      },
      searchFieldGroup: {
        display: 'flex',
        // alignContent: 'center',
        width: '100%',
        height: 32,
        backgroundColor: grey100,
        borderRadius: 1,
      },
      toolBarGroupInput: {
        marginLeft: 10,
        // width: (this.props.designType===5) ? '100%' : '70%',
      },
    }

    let searchHint = 'Search...';

    let filtersIcon, navigationMenuIcon, searchIcon, sortIcon, searchFiltersTop;

    const _filtersIcon = (
      <IconButton
        onClick={this.handleFiltersButtonClick}
        style={styles.filtersButton}
        // tooltip={this.state.showFilters ? 'Hide filters' : 'Show filters'}
       >
        {this.state.showFilters ? <ExpandLess color={styles.filtersIconColor.color} /> : <ExpandMore color={styles.filtersIconColor.color} />}
      </IconButton>
    );

    const _navigationMenuIcon = (
      <IconButton
        onClick={this.handleMenuButtonClick}
      // tooltipPosition='bottom-right'
      >
        <NavigationMenu color={styles.navBarIcons.color} />
      </IconButton>
    );

    const _navigationMoreVertIcon = (
      <IconButton
      // onClick={this.handleMenuButtonClick}
      // tooltip="Sign in"
      // touch={true}
      // tooltipPosition='bottom-left'
      >
        <NavigationMoreVert color={styles.navBarIcons.color} />
      </IconButton>
    );

    const _searchIcon = (
      <IconButton
        onClick={() => this.props.fetchImages()}
      >
        <ActionSearch color={styles.navBarIcons.color} />
      </IconButton>
    );

    const _searchFiltersTop = (
      <div style={styles.searchFiltersTop}>
        <SearchFilters />
      </div>
    );

    if (this.props.designType===5) {
      styles.toolBarGroupInput.width = '100%';
      searchIcon = null;
      sortIcon = null;
      filtersIcon = _filtersIcon;
      navigationMenuIcon = true;
    } else {
      styles.toolBarGroupInput.width = '70%';
      sortIcon = <ImgSortIcon />;
      searchIcon = _searchIcon;
      searchFiltersTop = _searchFiltersTop;
      navigationMenuIcon = false;
      if (this.state.windowTop) {
        filtersIcon = null;
      } else {
        filtersIcon = _filtersIcon;
      }
    }

    console.log(this.state.showFilters);

    return (
      <div>
        <div style={styles.rootComponent}>
          <Toolbar 
            style={styles.navBar}>
            <ToolbarGroup firstChild={true}>
              {/* <IconButton 
                onClick={this.handleMenuButtonClick}
                // tooltip="Open menu"
                // touch={true}
                // tooltipPosition='bottom-right'
              >
                <NavigationMenu color={styles.navBarIcons.color} />
              </IconButton> */}
              {(!navigationMenuIcon) ? _navigationMenuIcon : null} 
            </ToolbarGroup>
            <ToolbarGroup style={styles.toolBarGroupInput}>
              {searchIcon}
              <div style={styles.searchFieldGroup}>
                <TextField
                  name='searchText'
                  value={this.props.searchText}
                  onChange={(e) => { this.props.fetchSearchText(e.target.value); this.props.fetchImages() }}
                  hintText={searchHint}
                  hintStyle={styles.hintStyle}
                  inputStyle={styles.inputStyle}
                  fullWidth={true}
                  underlineShow={false}
                  style={styles.searchField}
                  autoFocus
                />
                {filtersIcon}
              </div>
              {sortIcon}
            </ToolbarGroup>
            <ToolbarGroup lastChild={true}>    
              {/* <IconButton 
                // onClick={this.handleMenuButtonClick}
                // tooltip="Sign in"
                // touch={true}
                // tooltipPosition='bottom-left'
              >
                <NavigationMoreVert color={styles.navBarIcons.color} />
              </IconButton> */}
              {_navigationMoreVertIcon}
              {navigationMenuIcon ? _navigationMenuIcon : null} 
            </ToolbarGroup>
          </Toolbar>
          {/* <SearchFilters/> */}
          {/* <FiltersBar /> */}
          {this.state.showFilters ? <SearchFilters /> : null}
          {/* <Divider /> */}
          <MenuDrawer 
            value={this.state.showDrawer} 
            side={navigationMenuIcon}
            onChange={(val) => this.setState({ showDrawer: val })} 
          />
        </div>
        {searchFiltersTop}
      </div>  
    )
  }
}

NavBar.propTypes = {
  fetchSearchText: PropTypes.func.isRequired,
  fetchImages: PropTypes.func.isRequired,
  fetchWindowWidth: PropTypes.func.isRequired,
  searchText: PropTypes.string.isRequired,
  designType: PropTypes.number.isRequired,

  // windowStartInnerWidth: PropTypes.number,
}

const mapStateToProps = state => ({
  searchState: state,
  searchText: state.search.searchText,
  designType: state.search.designType,
  // windowStartInnerWidth: state.search.windowStartInnerWidth,
})

export default connect(mapStateToProps, { fetchImages, fetchSearchText, fetchWindowWidth })(NavBar)


