import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { fetchImages } from '../../actions/searchAction';
import { fetchOrder } from '../../actions/searchAction';

import CustomStyles from '../../CustomMaterialUIStyles/FilterComponents';
// import {Filters} from '../../CustomMaterialUIStyles/FilterComponents';

import SelectField from 'material-ui/SelectField';
import MenuItem from 'material-ui/MenuItem';
// import ArrowDropRight from 'material-ui/svg-icons/navigation-arrow-drop-right';

// const styles = { 
// };

class ImgOrder extends Component {
  render() {
    let styles = {
      _dropDownMenuStyle: {},
    };

    if (this.props.designType === 5) {
      styles._dropDownMenuStyle.menuStyle = { width: '100vw' }
    }
    
    return (
      <SelectField
        name='order'
        className='SelectField'
        value={this.props.order}
        onChange={(e, index, value) => { this.props.fetchOrder(value); this.props.fetchImages() }}
        // onChange={(e, index, value) => { this.props.fetchOrder(value); this.props.fetchImages() }}
        // floatingLabelText='Sort by'
        style={CustomStyles.rootSelectField}
        labelStyle={CustomStyles.labelSelectField}
        iconStyle={CustomStyles.iconSelectField}
        underlineStyle={CustomStyles.underlineSelectField}
        dropDownMenuProps={Object.assign({}, CustomStyles.dropDownMenuProps, styles._dropDownMenuStyle)}
        listStyle={CustomStyles.selectFieldList}
        // menuStyle={styles.selectFieldMenuItem}
        // menuItemStyle={styles.selectFieldMenuItem}
      >
        <MenuItem value='popular' primaryText='popular' innerDivStyle={CustomStyles.selectFieldMenuItem} />
        <MenuItem value='latest' primaryText='latest' innerDivStyle={CustomStyles.selectFieldMenuItem} />
      </SelectField>
    )
  }
}

ImgOrder.propTypes = {
  fetchImages: PropTypes.func.isRequired,
  fetchOrder: PropTypes.func.isRequired,
  order: PropTypes.string.isRequired,
}

const mapStateToProps = state => ({
  order: state.search.order,
})

export default connect(mapStateToProps, { fetchImages, fetchOrder })(ImgOrder)


