import { FETCH_VIEWERIMG, FETCH_VIEWEROPEN, FETCH_WAIT_NEXT_DATA } from '../actions/types';

const initialState = {
  open: false, 
  currentImgInd: -1,
  nextDataLoading: false,
};

export default function (state = initialState, action) {
  switch (action.type) {
    case FETCH_VIEWEROPEN:
      return Object.assign({}, state, {
        open: action.payload[0],
        currentImgInd: action.payload[1],
      })
    case FETCH_VIEWERIMG:
      console.log('FETCH_VIEWERIMG')
      return Object.assign({}, state, {
        currentImgInd: action.payload,
        open: true,
        nextDataLoading: false,
      })
    case FETCH_WAIT_NEXT_DATA:
      return Object.assign({}, state, {
        nextDataLoading: true,
      })
    default:
      return state;
  }
}



