import React, { Component } from 'react';
import { connect } from 'react-redux';

import ImageResults from './ImageResults';

import { grey100, grey500 } from 'material-ui/styles/colors';

class Search extends Component {
  constructor(props) {
    super(props);
    this.state = {
      orientation: 'all',
      total: 0,
    };
  }

  render() {
    console.log(this.state);

    const styles = {
      rootComponent: {
        position: 'relative',
        top: 57,
      },
      iconButton: {
        float: 'left',
        paddingTop: 17
      },
      floatingLabelStyle: {
        fontSize: 19,
        textAlign: 'right',
        width: '99%',
        // transform: 'scale(0.75) translate(-15px, -28px)',
        transformOrigin: 'right top 0px',
      },
      hintStyle: {
        // color: white,
        fontSize: 17,
        // textAlign: 'center',
        // width: '100%',
        // margin: '0px 0px 10px 0px',

        height: 16,
        paddingLeft: 5,
        color: grey500
      },
      inputStyle: {
        fontSize: 17,
        // color: white,
        //   // textAlign: 'center',
        //   // width: '100%',
        //   // margin: '0px 0px 20px 0px',
        //   borderRadius: 2,
        //   // backgroundColor: grey700,
        //   height: 35,
        paddingLeft: 5
      },
      textField: {
        backgroundColor: grey100,
        borderRadius: 1,
        height: 34,
      },
    };

    const imageResultsValue = {
      orientation: this.state.orientation,
      total: this.state.total,
    };

    return (
      <div style={styles.rootComponent}>
        {/* {this.props.images.length > 0 ? <ImageResults images={this.props.images} value={imageResultsValue}/> : null} */}
        {this.props.images.length > 0 ? <ImageResults value={imageResultsValue} /> : null}
      </div>
    )
  }
}

const mapStateToProps = state => ({
  images: state.search.images,
})

export default connect(mapStateToProps)(Search)


