import { FETCH_VIEWERIMG, FETCH_VIEWEROPEN, FETCH_WAIT_NEXT_DATA } from './types';

export const fetchViewerOpen = (boolean, currentImgInd) => (dispatch) => {
  console.log('fetchViewerOpen (boolean, currentImgInd) : ', boolean, currentImgInd);
  dispatch({
    type: FETCH_VIEWEROPEN,
    payload: [boolean, currentImgInd],
  })
}

export const fetchViewerImg = (newImgInd = null) => (dispatch, getState) => {
  console.log('fetchViewerImg (newImgInd)', newImgInd);
  if(newImgInd === null) {
    newImgInd = getState().imgViewer.currentImgInd + 1;
  }
  console.log('fetchViewerImg (newImgInd)', newImgInd);
  dispatch({
    type: FETCH_VIEWERIMG,
    payload: newImgInd,
  })
}

export const fetchWaitNextData = () => (dispatch) => {
  dispatch({
      type: FETCH_WAIT_NEXT_DATA,
  })
}


